import { createReducer } from "@reduxjs/toolkit";
import { ppes } from "../mocks/ppe";
import { selectPPE } from "./action";
var initialState = {
    ppe: null,
    ppes: ppes
};
export var reducer = createReducer(initialState, function (builder) {
    builder
        .addCase(selectPPE, function (state, action) {
        state.ppe = action.payload.ppe;
    });
});
